import { lazy } from 'react'
import { Redirect } from 'react-router-dom'

const AppRoutes = [
  {
    path: '/question/add',
    component: lazy(() => import('../../views/question/add/questionAddComponentContainer'))
  },
  {
    path: '/question/edit',
    component: lazy(() => import('../../views/question/edit/questionEditComponentContainer'))
  },
  {
    path: '/user/view',
    exact: true,
    component: () => <Redirect to='/user/view/1' />
  },
  {
    path: '/user/view/:id',
    component: lazy(() => import('../../views/users/view')),
    meta: {
      navLink: '/user/view'
    }
  }
]

export default AppRoutes
